import React from 'react';
import arrowDownTwo from '../../svg/two-brown.png';
import { Link } from 'react-scroll';

const ArrowDownTwo = () => {
    return (
            <Link to='description'smooth={true}duration={1000}><img className='arrowDownTwo' src={arrowDownTwo} alt='Arrow Down'></img></Link>
    )
}

export default ArrowDownTwo;
