import React from 'react';
import arrowDownFive from '../../svg/two-brown.png';
import { Link } from 'react-scroll';

const ArrowSix = () => {
    return (
        <div>
            <Link to='gallery'smooth={true}duration={1000}><img className='arrowDownFive' src={arrowDownFive} alt='Arrow Down'></img></Link>
        </div>
    )
}

export default ArrowSix;