import React from 'react';
import arrowDownSix from '../../svg/two-brown.png';
import { Link } from 'react-scroll';

const ArrowDownSix = () => {
    return (
        <Link to='contact'smooth={true}duration={1000}><img src={arrowDownSix} className='arrowDownSix' alt='Arrow Down'></img></Link>
    )
}

export default ArrowDownSix;
