import React from 'react';
import arrowDownOne from '../../svg/two-brown.png';
import { Link } from 'react-scroll';

const ArrowDown = () => {
    return (
            <Link to='about'smooth={true}duration={1000}><img className='arrowDown'src={arrowDownOne}alt='Arrow Down'></img></Link>
    )
}

export default ArrowDown;
