import React from 'react';
import arrowDownFour from '../../svg/two-brown.png';
import { Link } from 'react-scroll';
 
const ArrowDownFour = () => {
    return (
        <Link to='menu'smooth={true}duration={1000}><img src={arrowDownFour} className='arrowDownFour' alt='Arrow Down'></img></Link>
    )
}

export default ArrowDownFour;
